:root {
  --Brick_Red: #b7410e;
  --Terracotta: #c1440e;
  --primary: #8b4513;
  --secondary: #d2691e;
  --accent: #cd853f;
  --Deep_Red: #8b1a1a;
  --Muted_Brick: #a23e2d;
  --Orange: #ff6f00;
  --Charcoal_Gray: #333333;
  --Concrete_Gray: #7d7d7d;
  --Dark_Slate: #2c2c2c;
  --dark: #2f4f4f;
  --darker: #1a2f2f;
  --Mustard_Yellow: #ffd447;
  --Gold_Accent: #c9a227;
  --Steel_Blue: #4682b4;
  --light: #f5f5dc;
  --Light_Gray: #f2f2f2;
  --Sand: #f5f5dc;
  --Beige: #e5d3b3;
  --White: #ffffff;
  --Cream_White: #faf3e0;
}
.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/mason01.webp");
  background-size: cover;
  background-position: center;
  padding: 100px 0;
  text-align: center;
  color: var(--light);
}
.section-title {
  position: relative;
  margin-bottom: 40px;
  font-weight: 700;
  color: var(--dark);
  text-align: center;
}
.section-title:after {
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  background: var(--primary);
  margin: 15px auto;
}
.section-content {
  padding: 25px 0;
}
.stats-section {
  background-color: var(--dark);
  color: white;
  padding: 30px;
}
.stat-number {
  font-size: 3rem;
  font-weight: 700;
  color: var(--Steel_Blue);
}

/* NAV */
.top {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  color: var(--Deep_Red);
  background-color: var(--Sand);
}
.top_left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  gap: 10px;
}
.top_right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  gap: 10px;
}
.top_seperator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-left: 1px solid var(--Deep_Red);
  padding: 0 10px;
  cursor: pointer;
}
.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
}
.nav_left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}
.nav_right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
}
.nav_seperator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-left: 1px solid var(--Deep_Red);
  padding: 0 10px;
  cursor: pointer;
}
@media (max-width: 1350px) {
  .top {
    display: none;
  }
}
.bg-body-tertiary {
  width: 100% !important;
  padding: 5px !important;
  align-items: center !important;
  justify-content: center !important;

  background-color: var(--Beige) !important;
}

/* Section 01 */
.sectionone {
  width: 100%;
  height: 700px;
  margin: 0;
  padding: 0;
  /* height:fit-content; */
}
.slide_image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 20%;
  width: 100%;
  height: 700px;
}
.breadcrumb {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
p {
  text-align: left;
  text-wrap: wrap;
  width: 60%;
}
.carousel_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.cta_buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
/* Section 02 */
.service-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: var(--primary);
  margin-bottom: 15px;
}
p {
  text-align: left;
  padding: 0;
  margin: 15px;
  font-size: larger;
}
.highlight {
  color: var(--primary);
  font-weight: 600;
}
.card {
  background-color: var(--Cream_White);
  border-radius: 8px;
  /* height: fit-content; */
}
.testimonial-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin: 20px 0;
}
.testimonial-text {
  font-style: italic;
  position: relative;
  padding: 0 20px;
}
.testimonial-text:before,
.testimonial-text:after {
  content: " ";
  font-size: 50px;
  color: var(--accent);
  position: absolute;
}
.testimonial-text:before {
  left: -10px;
  top: -20px;
}
.testimonial-text:after {
  content: "";
  right: -10px;
  bottom: -40px;
}
.client-name {
  font-weight: 600;
  color: var(--primary);
  margin-top: 20px;
}
.step-number {
  width: 60px;
  height: 60px;
  background-color: var(--primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 auto 20px;
}
.accordion-button:not(.collapsed) {
  background-color: var(--primary);
  color: var(--Sand);
}
.accordion-button:focus {
  box-shadow: none;
  border-color: var(--Sand);
}
.footer {
  background-color: var(--darker);
  color: white;
  padding: 60px 0 20px;
}
.footer h5 {
  color: var(--secondary);
  font-weight: 700;
  margin-bottom: 25px;
  position: relative;
}
.contact-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 20px;
  padding: 25px 0;
}
.contact-card {
  padding: 10px;
  border-radius: 15px;
  width: 500px;
  max-width: 500px;
  min-width: 350px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}
.contact-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: var(--primary);
  margin-bottom: 20px;
}
/* Services */
.service-card {
  /* height: 1200px; */
  height: fit-content;
}

/* Gallery */
.gallery_container {
  padding: 25px;
}
.gallery-header {
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/mason01.webp");
  background-size: cover;
  background-position: center;
  padding: 100px 0;
  text-align: center;
  color: white;
}
.section-title {
  position: relative;
  margin-bottom: 40px;
  font-weight: 700;
  color: var(--dark);
  text-align: center;
}
.section-title:after {
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  background: var(--primary);
  margin: 15px auto;
}
.gallery-content {
  padding: 80px 0;
}
.filter-buttons {
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}
.filter-btn {
  background-color: white;
  color: var(--dark);
  border: 2px solid var(--primary);
  padding: 10px 25px;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
}
.filter-btn:hover,
.filter-btn.active {
  background-color: var(--primary);
  color: white;
}
.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}
.gallery-item {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  cursor: pointer;
  height: 250px;
}
.gallery-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}
.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}
.gallery-item:hover .gallery-img {
  transform: scale(1.1);
}
.gallery-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 60%);
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: flex-end;
  padding: 20px;
  color: white;
}
.gallery-item:hover .gallery-overlay {
  opacity: 1;
}
.gallery-caption {
  transform: translateY(20px);
  transition: transform 0.3s ease;
}
.gallery-item:hover .gallery-caption {
  transform: translateY(0);
}
.modal-content {
  border-radius: 12px;
  overflow: hidden;
}
.modal-img {
  width: 100%;
  border-radius: 8px;
}
.cta-section {
  background-color: var(--dark);
  color: white;
  padding: 80px 0;
  text-align: center;
}
.gallery-item {
  animation: fadeIn 0.5s ease;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 768px) {
  .gallery-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 15px;
  }

  .filter-buttons {
    gap: 10px;
  }

  .filter-btn {
    padding: 8px 16px;
    font-size: 0.9rem;
  }
}

/* Contact US */
.map-container {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  height: 500px;
  width: 500px;
  min-width: 300px;
  max-width: 350px;
}
.map-container iframe {
  width: 100%;
  height: 100%;
  min-height: 300px;
  border: none;
}
.contact-form {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  padding: 30px;
}
.form-control:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 0.25rem rgba(139, 69, 19, 0.25);
}
.social-links li {
  display: inline-block;
  width: 40px;
  height: 40px;
  background-color: var(--primary);
  color: white;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  margin-right: 10px;
  transition: all 0.3s ease;
}
.social-links li:hover {
  background-color: var(--secondary);
  transform: translateY(-3px);
}
@media (max-width: 768px) {
  .contact-info-card,
  .map-container {
    margin-bottom: 30px;
  }
}

/* Footer */
.footer-about p {
  line-height: 1.8;
  margin-bottom: 20px;
}
.footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-links li {
  margin-bottom: 12px;
}
.footer-links li:hover {
  color: var(--secondary);
  padding-left: 5px;
}
.footer-links li {
  color: #ccc;
  text-decoration: none;
  transition: all 0.3s ease;
  display: block;
}

.footer-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  /* gap: 200px; */
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 20px;
  margin-top: 40px;
}
.footer-bottom .col {
  margin: 0;
  color: #aaa;
  font-size: 0.9rem;
}
.footer-policies {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.social-icons {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}
.social-icons li:hover {
  background-color: var(--primary);
  transform: translateY(-3px);
}
.social-icons li {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  color: white;
  transition: all 0.3s ease;
}
.footer-contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}
.fcr {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}
.btn {
  padding: 12px 30px;
  margin: 20px;
  border-radius: 5px;
  background-color: transparent;
  transition: background-color 0.3s;
  border: 1px solid #fff;
}
.btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.btn-primary {
  padding: 12px 30px;
  border-radius: 5px;

  font-weight: 600;

  background-color: var(--primary);
  border-color: var(--dark);
  transition: background-color 0.3s;
}
.btn-primary:hover {
  background-color: var(--dark);
}
.blue {
  background-color: var(--Steel_Blue);
  color: var(--Sand);
}
.sand {
  background-color: var(--Sand);
  color: black;
}
