

.error-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.error-content {
  text-align: center;
  max-width: 800px;
  padding: 40px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.error-icon {
  font-size: 6rem;
  color: var(--primary);
  margin-bottom: 20px;
}

.error-title {
  font-size: 5rem;
  font-weight: 800;
  color: var(--dark);
  margin-bottom: 10px;
}

.error-subtitle {
  font-size: 1.8rem;
  color: var(--primary);
  margin-bottom: 20px;
  font-weight: 600;
}

.error-message {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 30px;
  color: #555;
}

.brick-wall {
  display: flex;
  justify-content: center;
  margin: 30px 0;
  flex-wrap: wrap;
  gap: 8px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.brick {
  width: 80px;
  height: 40px;
  background-color: var(--primary);
  border-radius: 4px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 0.8rem;
}

.brick:nth-child(2n) {
  transform: translateY(20px);
  background-color: var(--secondary);
}

.brick:nth-child(3n) {
  background-color: var(--accent);
}

.action-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 30px;
  flex-wrap: wrap;
}

.btn-primary {
  background-color: var(--primary);
  border-color: var(--primary);
  padding: 12px 30px;
  font-weight: 600;
  border-radius: 5px;
}

.btn-primary:hover {
  background-color: var(--secondary);
  border-color: var(--secondary);
}

.btn-outline {
  background-color: transparent;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 12px 30px;
  font-weight: 600;
  border-radius: 5px;
}

.btn-outline:hover {
  background-color: var(--primary);
  color: white;
}

.search-box {
  max-width: 400px;
  margin: 30px auto;
  position: relative;
}

.search-input {
  width: 100%;
  padding: 15px 20px;
  border: 2px solid #ddd;
  border-radius: 50px;
  font-size: 1rem;
  outline: none;
  transition: all 0.3s ease;
}

.search-input:focus {
  border-color: var(--primary);
}

.search-button {
  position: absolute;
  right: 5px;
  top: 5px;
  background: var(--primary);
  border: none;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.footer {
  text-align: center;
  padding: 20px;
  /* color: #666; */
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .error-title {
    font-size: 4rem;
  }

  .error-subtitle {
    font-size: 1.5rem;
  }

  .error-message {
    font-size: 1rem;
  }

  .action-buttons {
    flex-direction: column;
    align-items: center;
  }

  .btn-primary,
  .btn-outline {
    width: 100%;
    max-width: 300px;
  }

  .brick {
    width: 60px;
    height: 30px;
    font-size: 0.6rem;
  }
}

/* Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.error-content {
  animation: fadeIn 0.5s ease;
}

/* Masonry tool animation */
.tools-animation {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin: 30px 0;
}

.tool {
  font-size: 2.5rem;
  color: var(--primary);
  animation: bounce 2s infinite;
}

.tool:nth-child(2) {
  animation-delay: 0.3s;
  color: var(--secondary);
}

.tool:nth-child(3) {
  animation-delay: 0.6s;
  color: var(--accent);
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}
